* {
  margin: 0;
  padding: 0;
}

.container {
  background: linear-gradient(to right bottom, #e1b382, #c89666);
  width: 100vw;
  min-width: fit-content;
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.app-wrapper {
 background-color: #12343b;
 padding: 3%;
 box-sizing: border-box;
 min-width: fit-content;
 border-radius: 10px;
 box-shadow: 3px 6px 40px #000;
 margin-bottom: 10px;
}

.header h1 {
  color: #fff;
  text-align: center;
  margin: 30px 0;
}

.form {
 display: flex;
 width: fit-content;
 justify-content: space-between;
 white-space: nowrap;
}

.task-input {
 outline: none;
 padding: 15px;
 font-size: 20px;
 color: #ccc;
 background-color: #000;
 border: 1px solid #c89666;
 border-radius: 10px;
 flex: 1;
}

.button-add {
 padding: 15px;
 font-size: 20px;
 border-radius: 10px;
 border: none;
 background-color: #f1af71;
 cursor: pointer;
 margin-left: 6%;
}

.list-item {
  display: flex;
  margin: 20px 0;
  width: fit-content;
  justify-content: space-between;
  border: 1px solid #ccc;
  border-radius: 10px;
  padding: 10px 20px;
  max-height: 30px;
}

.action-btns {
 display: flex;
 white-space: nowrap;
}

.button-complete,
.button-delete,
.button-edit {
  border: none;
  outline: none;
  cursor: pointer;
  background-color: transparent;
  font-size: 28px;
  border-radius: 50%;
}

.button-complete {
  color: #ff6c6c;
  margin-right: 6%;
}

.button-edit {
  color: #e2d029;
  margin-right: 6%;
}

.button-delete {
  color: lightseagreen;
}

.complete {
  text-decoration-style: solid;
  text-decoration-line: line-through;
  text-decoration-color: #ff6c6c;
  opacity: 0.6;
}

.list {
  width: 220px;
  background-color: #12343b;
  border: none;
  color: #ccc;
  font-size: 20px;
  padding-left: 10px;
  margin-right: 15px;
}

input:focus,
button:focus {
  outline: none;
}

/* Default styles for larger screens */
@media (min-width: 600px) {
 .app-wrapper {
   min-width: 450px;
   min-height: 650px;
 }
}

/* Styles for medium-sized screens */
@media (max-width: 599px) and (min-width: 400px) {
 .app-wrapper {
   min-width: 350px;
   min-height: 650px;
 }
}

/* Styles for small screens */
@media (max-width: 399px) {
 .app-wrapper {
   min-width: 100%;
   min-height: 650px;
 }
}